import { Component, ViewEncapsulation } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { UsersService } from '../services/UsersService';
@Component({
  selector: '[email-user]',
  template: require('./email-user.template.html'),
  encapsulation: ViewEncapsulation.None,
  providers: [UsersService],
  styles: [require('./email-user.style.scss')]
})
export class EmailUser {
  newUserPassword: string;
  newUserConfirmPassword: string;
  router: Router;
  email: string;
  userCode: string;
  constructor(private activatedRoute: ActivatedRoute, private usersService: UsersService, router: Router) {
    this.activatedRoute.queryParams.subscribe((params: Params) => {
      this.email = params['email'];
      this.userCode = params['userCode'];
      console.log(this.email + " " + this.userCode);
      this.router = router;
      this.usersService.authenticateToken().subscribe(
        a => {
          this.usersService.checkLinkValidity(this.email, this.userCode, a.token).subscribe(
            b => {
              console.log('check');
              if (b.code == 600) {
                router.navigate(['/login']);
              }
              else {
                sessionStorage.setItem('token', a.token);
                sessionStorage.setItem('userId', b.data._id);
                sessionStorage.setItem('role', b.data.role);
                sessionStorage.setItem('userObject', JSON.stringify(b.data));
              }
            }
          )
        });
    });
  }
  sendAnother() {
    var flag = false;
    var password = this.newUserPassword;
    if (password.match(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,}$/)) {
      jQuery("#passwordComplexityError").css("display", "none");
    } else {
      flag = true;
      jQuery("#passwordComplexityError").css("display", "block");
      jQuery("#passwordComplexityError").text("The password should contain Minimum 8 characters, at least 1 Uppercase Alphabet, 1 Lowercase Alphabet and 1 Number");
    }
    if (this.newUserPassword !== this.newUserConfirmPassword) {
      flag = true;
      jQuery(".error-msg").text("Passwords do not match, check again");
    }
    if (this.newUserPassword !== "") {
      jQuery(".error-msg").text("Field Can't be Empty.");
    }
    if (flag == false) {
      this.usersService.changePassword(this.email, this.newUserPassword, sessionStorage.getItem('token')).subscribe(
        b => {
          if (b.code == 200) {
            console.log('call');
            this.router.navigate(['/app/dashboard']);
          }
        });
    }
  }
}



